import { HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { CallNumber } from '@ionic-native/call-number';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio';
import { GoogleMaps } from "@ionic-native/google-maps";
import { Keyboard } from '@ionic-native/keyboard';
import { NativePageTransitions } from '@ionic-native/native-page-transitions';
import { SocialSharing } from '@ionic-native/social-sharing';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';

import firebase from 'firebase';

import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { StarRatingModule } from 'ionic3-star-rating';

import { IonMarqueeModule } from "../ion-marquee";
import { AuthProvider } from '../providers/auth/auth';
import { CartProvider } from '../providers/cart/cart';
import { CategoryProvider } from '../providers/category/category';
import { OrderProvider } from '../providers/order/order';
import { ProductsProvider } from '../providers/products/products';
import { UserData } from '../providers/user-data';
import { ActivityService } from "../services/activity-service";
import { TripService } from "../services/trip-service";
import { WeatherProvider } from "../services/weather";
import { config } from './../config/app.config';
import { MyApp } from "./app.component";
import { UserProvider } from '../providers/user/user';


firebase.initializeApp(config.firebaseConfig);

// import services
// end import services
// end import services

// import pages
// end import pages

@NgModule({
  declarations: [
    MyApp

  ],
  imports: [
    BrowserModule,
    IonMarqueeModule,
    HttpClientModule,
    StarRatingModule,
    IonicModule.forRoot(MyApp, {
      scrollPadding: false,
      scrollAssist: true,
      autoFocusAssist: false,
      HttpModule
    }, 
{
  links: [
    { loadChildren: '../pages/cart/cart.module.ngfactory#CartPageModuleNgFactory', name: 'CartPage', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/catalog/catalog.module.ngfactory#CatalogPageModuleNgFactory', name: 'CatalogPage', segment: 'catalog', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/category/category.module.ngfactory#CategoryPageModuleNgFactory', name: 'CategoryPage', segment: 'category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/checkout/checkout.module.ngfactory#CheckoutPageModuleNgFactory', name: 'CheckoutPage', segment: 'checkout', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/dashboard/dashboard.module.ngfactory#DashboardPageModuleNgFactory', name: 'DashboardPage', segment: 'dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-profile/edit-profile.module.ngfactory#EditProfilePageModuleNgFactory', name: 'EditProfilePage', segment: 'edit-profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/local-weather/local-weather.module.ngfactory#LocalWeatherPageModuleNgFactory', name: 'LocalWeatherPage', segment: 'local-weather', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notifications/notifications.module.ngfactory#NotificationsPageModuleNgFactory', name: 'NotificationsPage', segment: 'notifications', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/order-details/order-details.module.ngfactory#OrderDetailsPageModuleNgFactory', name: 'OrderDetailsPage', segment: 'order-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/order-history/order-history.module.ngfactory#OrderHistoryPageModuleNgFactory', name: 'OrderHistoryPage', segment: 'order-history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/orders/orders.module.ngfactory#OrdersPageModuleNgFactory', name: 'OrdersPage', segment: 'orders', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/send-sms/send-sms.module.ngfactory#SendSmsPageModuleNgFactory', name: 'SendSmsPage', segment: 'send-sms', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsPageModuleNgFactory', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single/single.module.ngfactory#SinglePageModuleNgFactory', name: 'SinglePage', segment: 'single', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/store/store.module.ngfactory#StorePageModuleNgFactory', name: 'StorePage', segment: 'store', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/surplus/surplus.module.ngfactory#SurplusPageModuleNgFactory', name: 'SurplusPage', segment: 'surplus', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tutorial/tutorial.module.ngfactory#TutorialPageModuleNgFactory', name: 'TutorialPage', segment: 'tutorial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user-details/user-details.module.ngfactory#UserDetailsPageModuleNgFactory', name: 'UserDetailsPage', segment: 'user-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/users/users.module.ngfactory#UsersPageModuleNgFactory', name: 'UsersPage', segment: 'users', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot({
      name: '__Get_Rest',
      driverOrder: ['indexeddb', 'sqlite', 'websql']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    ActivityService,
    TripService,
    NativePageTransitions,
    WeatherProvider,
    FingerprintAIO,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    CallNumber,
    SocialSharing,
    GoogleMaps,
    UserData,
    ProductsProvider,
    AuthProvider,
    CartProvider,
    CategoryProvider,
    OrderProvider,
    UserProvider
  ]
})

export class AppModule {
}



